.experience {
  position: relative;
  min-height: 900px;
  padding: 100px 0;

  &::after {
    position: absolute;
    left: 0;
    bottom: 0;
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background-color: var(--gray-color);
  }

  &__title {
    margin-bottom: 40px;
  }

  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
  }

  &__text {
    width: 435px;
  }

  &__img   {
    &-front {
      width: 400px;
      height: 487px;
    }
  }

  &__img   {
    &-back {
      position: absolute;
      top: -65px;
      right: -80px;
      z-index: -5;
    }
  }
}