.nav {
  position: absolute;
  top: -35px;
  // right: -286px;
  right: 286px;
  z-index: 500;
  overflow-y: auto;
  padding: 50px 36px;
  // width: 286px;
  width: 0;
  height: 900px;
  background-color: var(--gray-color);
  // transform: translateX(100%);
  visibility: hidden;
  transition: width .2s ease-in-out;

  &__close {
    margin-left: 185px;
    margin-bottom: 46px;
    width: 30px;
    height: 30px;
    fill: var(--light-color);
  }

  &__item {
    margin-bottom: 27px;
  }

  &__link {
    font-family: 'Abel', sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 149.94%;

    letter-spacing: 0.13em;
    color: var(--light-color);
  }
}

.menu--active {
  // transform: translateX(0);
  right: 0;
  width: 286px;
  visibility: visible;
}