.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__label {
    display: flex;
    flex-direction: column;
  }

  &__input {
    justify-content: center;
    margin-bottom: 25px;
    padding: 10px;
    width: 350px;
    height: 40px;
    background-color: inherit;

    font-family: 'Abel', sans-serif;
    font-weight: 400;
    font-size: 15px;
    line-height: 19px;
    letter-spacing: 0.11em;

    color: var(--grayest-color);

    border: 1px solid #000000;

    @include mobile {
      width: 300px;
    }

    &:last-child {
      margin-bottom: 35px;
    }
  }

  &__btn {
    padding: 11px 40px;

    font-family: 'Abel', sans-serif;
    font-weight: 400;
    font-size: 13px;
    line-height: 17px;

    text-align: center;
    letter-spacing: 0.11em;
    text-transform: capitalize;

    color: #000000;

    border: 1px solid #000000;
  }
}