.reviews {
  position: relative;
  min-height: 900px;
  padding: 100px 0;

  &::after {
    position: absolute;
    left: 0;
    bottom: 0;
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background-color: var(--gray-color);
  }

  &__title {
    margin-bottom: 70px;
  }
}