.request {
  min-height: 545px;
  padding: 100px 0;

  &__logo {
    text-align: center;
    margin-bottom: 50px;
  }

  &__text {
    font-family: 'Abel', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 25px;
    text-align: center;
    letter-spacing: 0.11em;
    text-transform: uppercase;
  }

  &__info {
    margin-bottom: 40px;
  }
}