.about {
  min-height: 900px;
  padding: 100px 0;

  &__title {
    margin-bottom: 70px;
  }

  &__text {
    margin: 0;
    margin-bottom: 70px;
    font-weight: 200;
    font-size: 22px;
    line-height: 149.94%;
    letter-spacing: 0.13em;

    color: var(--grayest-color);

    &-dark {
      color: var(--dark-color);
    }

    &:last-child {
      margin-bottom: 0;
    }

    @include mobile {
      text-align: justify;
    }
  }
}