.burger {
  position: relative;
  top: 20px;
  z-index: 200;
  width: 36px;
  height: 17px;

  &__line {
    position: absolute;
    top: 0;
    right: 0;
    height: 2px;
    width: 36px;
    background-color: var(--dark-color);

    &:first-child {
      margin-top: 14px;
    }

    &:last-child {
      margin-top: 7px;
    }
  }
}