.footer {
  padding: 117px 0;
  min-height: 355px;
  background-color: var(--grayest-color);

  &__inner {
    display: flex;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
  }

  &__contact {
    margin-bottom: 22px;
    font-family: 'Abel', sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 25px;
    letter-spacing: 0.11em;
    text-decoration: none;
    color: var(--light-color);

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__name {
    margin-bottom: 20px;
    font-family: 'Abel', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 25px;
    letter-spacing: 0.11em;

    color: var(--light-color);
  }

  &__socials {
    display: flex;
    flex-direction: column;
    justify-content: end;
  }

  &__link {
    margin-top: 20px;
    font-family: 'Abel', sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 25px;
    letter-spacing: 0.11em;

    text-decoration: none;
    color: var(--light-color);
  }
}