.services {
  position: relative;
  min-height: 900px;
  padding: 100px 0;

  &::after {
    position: absolute;
    left: 0;
    bottom: 0;
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background-color: var(--gray-color);
  }

  &__inner {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin-bottom: 100px;
  }
}

.box {
  display: flex;
  height: 96px;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 30px 36px;
  border: 1px solid var(--dark-color);

  cursor: pointer;

  @include tablet900 {
    height: 250px;
  }

  &__info {
    // flex: 0 1 100%;
    visibility: hidden;
  }

  &__title {
    margin-bottom: 54px;
    font-family: 'Abel', sans-serif;
    font-size: 25px;
    line-height: 149.94%;
    letter-spacing: 0.13em;

    @include tablet {
      margin-bottom: 10px;
    }
  }

  &__cross {
    width: 37px;
    height: 37px;

    @include tablet900 {
      display: none;
    }
  }

  &.active {
    flex: 700px;

    @include tablet900 {
      padding: 10px;
      flex: 900px;
    }

    @include small-tablet {
      flex: 1600px;
      text-align: justify;
    }
  }

  &__text {
    margin-bottom: 20px;
    font-family: 'Abel', sans-serif;
    font-weight: 400;
    font-size: 15px;
    line-height: 149.94%;

    letter-spacing: 0.13em;

    &:last-child {
      margin-bottom: 0;
    }

    &-gray {
      color: var(--grayest-color);
    }
  }
}

.visible {
  visibility: visible;
}

.rotate {
  transform: rotate(45deg);
}